import React, { FC } from 'react'
import { IEnter } from '@hooks/useContext'
import { CurrencyList } from '@components/CurrencyList'
import { Button } from '@components/Button'
import { useHistory } from 'react-router'
import styles from '../styles.module.scss'

export const GiveOutResult: FC<IEnter> = (result) => {
  const history = useHistory()

  const onClickHandler = () => {
    history.push('/')
  }

  return (
    result.remain === 0
      ?
      <div>
        <p>–í—ã–¥–∞–Ω–æ:</p>
        <CurrencyList bills={result?.givenBills} />
        <div className={styles.leave_button}>
          <Button label='OK' onClick={onClickHandler} />
        </div>
      </div>
      :
      <div>
        <p>–í—ã–¥–∞–Ω–æ:</p>
        <CurrencyList bills={result?.givenBills} />
        <p>–ö —Å–æ–∂–∞–ª–µ–Ω–∏—é, –≤ –±–∞–Ω–∫–æ–º–∞—Ç–µ –Ω–µ—Ç –∫—É–ø—é—Ä—ã –Ω–æ–º–∏–Ω–∞–ª–æ–º –≤ {result.remain}. –í–∞–º –≤—ã–¥–∞–Ω–æ: {result.issued}. <br /> –ü—Ä–∏–Ω–æ—Å–∏–º –∏–∑–≤–∏–Ω–µ–Ω–∏—è –∑–∞ –¥–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω—ã–µ –Ω–µ—É–¥–æ–±—Å—Ç–≤–∞.</p>
        <div className={styles.leave_button} >
          <Button label='OK' onClick={onClickHandler} />
        </div>
      </div>
  )
}